.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {

    background: rgb(255, 255, 245);
    background: linear-gradient(0deg, rgba(255, 255, 245, 1) 0%, rgb(251, 222, 154) 100%);
    /* background: linear-gradient(0deg, rgba(255,255,245,1) 0%, rgba(255,233,183,1) 100%); */
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
