.li {
    color: rgb(107, 4, 4);
    display: flex;
    align-items: center;
    padding: .5rem 1rem;
    /* border: solid 1px rgb(177, 0, 0); */
    border-radius: 1px;
    margin-bottom: 1px;
    /* background-color: rgba(35, 35, 211, 0.15); */
    background-color: rgb(251, 251, 251);
    justify-content: space-between;
    border-radius: 10%;
}

.ul {
    list-style-type: none;
    text-align: left;
    padding-inline-start: 0px;
    background-color: rgb(251, 251, 251);
    margin-bottom: 10px;
    margin-top: 10px;
    box-shadow: 1px 1px 3px rgba(189, 47, 75, 0.3);
}

.logo {
    height: 150px;
}

.label {
    color: rgb(107, 4, 4);
    /* text-transform: italic; */
    /* font-family: Georgia, 'Times New Roman', Times, serif; */
    font-family: monospace;
    text-transform: capitalize;
    /* font-style: oblique; */
    font-weight: bold;
    font-size: 30px;
    margin: 25px;


}

.input {
    width: 80%;
    padding: 15px 22px;
    margin: 10px 5px;
    box-sizing: border-box;
    align-self: center;
}

.submit {
    background-color: blue;
    border: none;
    color: white;
    padding: 18px 36px;
    text-decoration: none;
    margin: 5px 4px;
    cursor: pointer;
}

.span {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
}

.done {
    text-decoration: line-through;
}

.delete {
    margin-left: 0;
    margin-right: auto;
    margin: 10px;
    position: relative;
    background: red;
    border-radius: 30%;
    border: none;
    color: white;

}

.index {
    padding: 1px;
    margin: 7px;
    text-shadow: 1px 1px 3px rgba(189, 47, 75, 0.3);
    margin-bottom: 3px;
    margin-top: 0px;
}

.post {
    text-emphasis: none;
    font-family: monospace;
    /* font-family: Georgia, 'Times New Roman', Times, serif; */
    word-wrap: normal;
    margin: 10px;
}
